.el-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  border-color: #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: .1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.el-button + .el-button {
    margin-left: 10px;
}
.el-button.is-round {
    padding: 12px 20px;
}
.el-button:hover, .el-button:focus {
    color: #46a0f0;
    border-color: #c8e3fb;
    background-color: #edf6fe;
}
.el-button:active {
    color: #3f90d8;
    border-color: #3f90d8;
    outline: none;
}
.el-button::-moz-focus-inner {
    border: 0;
}
.el-button [class*="el-icon-"] + span {
    margin-left: 5px;
}
.el-button.is-plain:hover, .el-button.is-plain:focus {
    background: #fff;
    border-color: #46a0f0;
    color: #46a0f0;
}
.el-button.is-plain:active {
    background: #fff;
    border-color: #3f90d8;
    color: #3f90d8;
    outline: none;
}
.el-button.is-active {
    color: #3f90d8;
    border-color: #3f90d8;
}
.el-button.is-disabled, .el-button.is-disabled:hover, .el-button.is-disabled:focus {
    color: #c0c4cc;
    cursor: not-allowed;
    background-image: none;
    background-color: #fff;
    border-color: #ebeef5;
}
.el-button.is-disabled.el-button--text {
    background-color: transparent;
}
.el-button.is-disabled.is-plain, .el-button.is-disabled.is-plain:hover, .el-button.is-disabled.is-plain:focus {
    background-color: #fff;
    border-color: #ebeef5;
    color: #c0c4cc;
}
.el-button.is-loading {
    position: relative;
    pointer-events: none;
}
.el-button.is-loading:before {
      pointer-events: none;
      content: '';
      position: absolute;
      left: -1px;
      top: -1px;
      right: -1px;
      bottom: -1px;
      border-radius: inherit;
      background-color: rgba(255, 255, 255, 0.35);
}
.el-button.is-round {
    border-radius: 20px;
    padding: 12px 23px;
}
.el-button--primary {
    color: #fff;
    background-color: #46a0f0;
    border-color: #46a0f0;
}
.el-button--primary:hover, .el-button--primary:focus {
      background: #6bb3f3;
      border-color: #6bb3f3;
      color: #fff;
}
.el-button--primary:active {
      background: #3f90d8;
      border-color: #3f90d8;
      color: #fff;
      outline: none;
}
.el-button--primary.is-active {
      background: #3f90d8;
      border-color: #3f90d8;
      color: #fff;
}
.el-button--primary.is-disabled, .el-button--primary.is-disabled:hover, .el-button--primary.is-disabled:focus, .el-button--primary.is-disabled:active {
      color: #fff;
      background-color: #a3d0f8;
      border-color: #a3d0f8;
}
.el-button--primary.is-plain {
      color: #46a0f0;
      background: #edf6fe;
      border-color: #b5d9f9;
}
.el-button--primary.is-plain:hover, .el-button--primary.is-plain:focus {
        background: #46a0f0;
        border-color: #46a0f0;
        color: #fff;
}
.el-button--primary.is-plain:active {
        background: #3f90d8;
        border-color: #3f90d8;
        color: #fff;
        outline: none;
}
.el-button--primary.is-plain.is-disabled, .el-button--primary.is-plain.is-disabled:hover, .el-button--primary.is-plain.is-disabled:focus, .el-button--primary.is-plain.is-disabled:active {
        color: #90c6f6;
        background-color: #edf6fe;
        border-color: #daecfc;
}
.el-button--success {
    color: #fff;
    background-color: #67c23a;
    border-color: #67c23a;
}
.el-button--success:hover, .el-button--success:focus {
      background: #85ce61;
      border-color: #85ce61;
      color: #fff;
}
.el-button--success:active {
      background: #5daf34;
      border-color: #5daf34;
      color: #fff;
      outline: none;
}
.el-button--success.is-active {
      background: #5daf34;
      border-color: #5daf34;
      color: #fff;
}
.el-button--success.is-disabled, .el-button--success.is-disabled:hover, .el-button--success.is-disabled:focus, .el-button--success.is-disabled:active {
      color: #fff;
      background-color: #b3e19d;
      border-color: #b3e19d;
}
.el-button--success.is-plain {
      color: #67c23a;
      background: #f0f9eb;
      border-color: #c2e7b0;
}
.el-button--success.is-plain:hover, .el-button--success.is-plain:focus {
        background: #67c23a;
        border-color: #67c23a;
        color: #fff;
}
.el-button--success.is-plain:active {
        background: #5daf34;
        border-color: #5daf34;
        color: #fff;
        outline: none;
}
.el-button--success.is-plain.is-disabled, .el-button--success.is-plain.is-disabled:hover, .el-button--success.is-plain.is-disabled:focus, .el-button--success.is-plain.is-disabled:active {
        color: #a4da89;
        background-color: #f0f9eb;
        border-color: #e1f3d8;
