.el-checkbox {
  color: #606266;
  font-weight: 500;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.el-checkbox.is-bordered {
    padding: 9px 20px 9px 10px;
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    box-sizing: border-box;
    line-height: normal;
    height: 40px;
}
.el-checkbox.is-bordered.is-checked {
      border-color: #46a0f0;
}
.el-checkbox.is-bordered.is-disabled {
      border-color: #ebeef5;
      cursor: not-allowed;
}
.el-checkbox.is-bordered + .el-checkbox.is-bordered {
      margin-left: 10px;
}
.el-checkbox.is-bordered.el-checkbox--medium {
      padding: 7px 20px 7px 10px;
      border-radius: 4px;
      height: 36px;
}
.el-checkbox.is-bordered.el-checkbox--medium .el-checkbox__label {
        line-height: 17px;
        font-size: 14px;
}
.el-checkbox.is-bordered.el-checkbox--medium .el-checkbox__inner {
        height: 14px;
        width: 14px;
}
.el-checkbox.is-bordered.el-checkbox--small {
      padding: 5px 15px 5px 10px;
      border-radius: 3px;
      height: 32px;
}
.el-checkbox.is-bordered.el-checkbox--small .el-checkbox__label {
        line-height: 15px;
        font-size: 12px;
}
.el-checkbox.is-bordered.el-checkbox--small .el-checkbox__inner {
        height: 12px;
        width: 12px;
}
.el-checkbox.is-bordered.el-checkbox--small .el-checkbox__inner::after {
          height: 6px;
          width: 2px;
}
.el-checkbox.is-bordered.el-checkbox--mini {
      padding: 3px 15px 3px 10px;
      border-radius: 3px;
      height: 28px;
}
.el-checkbox.is-bordered.el-checkbox--mini .el-checkbox__label {
        line-height: 12px;
        font-size: 12px;
}
.el-checkbox.is-bordered.el-checkbox--mini .el-checkbox__inner {
        height: 12px;
        width: 12px;
}
.el-checkbox.is-bordered.el-checkbox--mini .el-checkbox__inner::after {
          height: 6px;
          width: 2px;
}
.el-checkbox__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;
}
.el-checkbox__input.is-disabled .el-checkbox__inner {
      background-color: #edf2fc;
      border-color: #dcdfe6;
      cursor: not-allowed;
}
.el-checkbox__input.is-disabled .el-checkbox__inner::after {
        cursor: not-allowed;
        border-color: #c0c4cc;
}
.el-checkbox__input.is-disabled .el-checkbox__inner + .el-checkbox__label {
        cursor: not-allowed;
}
.el-checkbox__input.is-disabled.is-checked .el-checkbox__inner {
      background-color: #f2f6fc;
      border-color: #dcdfe6;
}
.el-checkbox__input.is-disabled.is-checked .el-checkbox__inner::after {
        border-color: #c0c4cc;
}
.el-checkbox__input.is-disabled.is-indeterminate .el-checkbox__inner {
      background-color: #f2f6fc;
      border-color: #dcdfe6;
}
.el-checkbox__input.is-disabled.is-indeterminate .el-checkbox__inner::before {
        background-color: #c0c4cc;
        border-color: #c0c4cc;
}
.el-checkbox__input.is-disabled + span.el-checkbox__label {
      color: #c0c4cc;
      cursor: not-allowed;
}
.el-checkbox__input.is-checked .el-checkbox__inner {
      background-color: #46a0f0;
      border-color: #46a0f0;
}
.el-checkbox__input.is-checked .el-checkbox__inner::after {
        -webkit-transform: rotate(45deg) scaleY(1);
                transform: rotate(45deg) scaleY(1);
}
.el-checkbox__input.is-checked + .el-checkbox__label {
      color: #46a0f0;
}
.el-checkbox__input.is-focus {
      /*focus时 视觉上区分*/
}
.el-checkbox__input.is-focus .el-checkbox__inner {
        border-color: #46a0f0;
}
.el-checkbox__input.is-indeterminate .el-checkbox__inner {
      background-color: #46a0f0;
      border-color: #46a0f0;
}
.el-checkbox__input.is-indeterminate .el-checkbox__inner::before {
        content: '';
        position: absolute;
        display: block;
        background-color: #fff;
        height: 2px;
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
        left: 0;
        right: 0;
        top: 5px;
}
.el-checkbox__input.is-indeterminate .el-checkbox__inner::after {
        display: none;
}
.el-checkbox__inner {
    display: inline-block;
    position: relative;
    border: 1px solid #dcdfe6;
    border-radius: 2px;
    box-sizing: border-box;
    width: 14px;
    height: 14px;
    background-color: #fff;
    z-index: 1;
    transition: border-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46), background-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46);
}
.el-checkbox__inner:hover {
      border-color: #46a0f0;
}
.el-checkbox__inner::after {
      box-sizing: content-box;
      content: "";
      border: 1px solid #fff;
      border-left: 0;
      border-top: 0;
      height: 7px;
      left: 4px;
      position: absolute;
      top: 1px;
      -webkit-transform: rotate(45deg) scaleY(0);
              transform: rotate(45deg) scaleY(0);
      width: 3px;
      transition: -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 0.05s;
      transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 0.05s;
      transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 0.05s, -webkit-transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6) 0.05s;
      -webkit-transform-origin: center;
              transform-origin: center;
}
.el-checkbox__original {
    opacity: 0;
    outline: none;
    position: absolute;
    margin: 0;
    width: 0;
    height: 0;
    left: -999px;
}
.el-checkbox__label {
    display: inline-block;
    padding-left: 10px;
    line-height: 19px;
    font-size: 14px;
