.el-tag {
  background-color: rgba(70, 160, 240, 0.1);
  display: inline-block;
  padding: 0 10px;
  height: 32px;
  line-height: 30px;
  font-size: 12px;
  color: #46a0f0;
  border-radius: 4px;
  box-sizing: border-box;
  border: 1px solid rgba(70, 160, 240, 0.2);
  white-space: nowrap;
}
.el-tag .el-icon-close {
    border-radius: 50%;
    text-align: center;
    position: relative;
    cursor: pointer;
    font-size: 12px;
    height: 16px;
    width: 16px;
    line-height: 16px;
    vertical-align: middle;
    top: -1px;
    right: -5px;
    color: #46a0f0;
}
.el-tag .el-icon-close::before {
      display: block;
}
.el-tag .el-icon-close:hover {
      background-color: #46a0f0;
      color: #fff;
}
.el-tag--info {
    background-color: rgba(144, 147, 153, 0.1);
    border-color: rgba(144, 147, 153, 0.2);
    color: #909399;
}
.el-tag--info.is-hit {
      border-color: #909399;
}
.el-tag--info .el-tag__close {
      color: #909399;
}
.el-tag--info .el-tag__close:hover {
      background-color: #909399;
      color: #fff;
}
.el-tag--success {
    background-color: rgba(103, 194, 58, 0.1);
    border-color: rgba(103, 194, 58, 0.2);
    color: #67c23a;
}
.el-tag--success.is-hit {
