.el-scrollbar {
  overflow: hidden;
  position: relative;
}
.el-scrollbar:hover > .el-scrollbar__bar, .el-scrollbar:active > .el-scrollbar__bar, .el-scrollbar:focus > .el-scrollbar__bar {
    opacity: 1;
    transition: opacity 340ms ease-out;
}
