.el-date-editor {
  position: relative;
  display: inline-block;
  text-align: left;
}
.el-date-editor.el-input, .el-date-editor.el-input__inner {
    width: 220px;
}
.el-date-editor--daterange.el-input, .el-date-editor--daterange.el-input__inner, .el-date-editor--timerange.el-input, .el-date-editor--timerange.el-input__inner {
    width: 350px;
}
.el-date-editor--datetimerange.el-input, .el-date-editor--datetimerange.el-input__inner {
    width: 400px;
}
.el-date-editor .el-icon-circle-close {
    cursor: pointer;
}
.el-date-editor .el-range__icon {
    font-size: 14px;
    margin-left: -5px;
    color: #c0c4cc;
    float: left;
    line-height: 32px;
}
.el-date-editor .el-range-input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    outline: none;
    display: inline-block;
    height: 100%;
    margin: 0;
    padding: 0;
    width: 39%;
    text-align: center;
    font-size: 14px;
    color: #606266;
}
.el-date-editor .el-range-input::-webkit-input-placeholder {
      color: #c0c4cc;
}
.el-date-editor .el-range-input:-ms-input-placeholder {
      color: #c0c4cc;
}
.el-date-editor .el-range-input::placeholder {
      color: #c0c4cc;
}
.el-date-editor .el-range-separator {
    display: inline-block;
    height: 100%;
    padding: 0 5px;
    margin: 0;
    text-align: center;
    line-height: 32px;
    font-size: 14px;
    width: 5%;
    color: #303133;
}
.el-date-editor .el-range__close-icon {
    font-size: 14px;
    color: #c0c4cc;
    width: 25px;
    display: inline-block;
    float: right;
    line-height: 32px;
}
.el-range-editor.el-input__inner {
  padding: 3px 10px;
}
.el-range-editor.is-active {
  border-color: #46a0f0;
}
.el-range-editor.is-active:hover {
    border-color: #46a0f0;
}
.el-range-editor--medium.el-input__inner {
  height: 36px;
}
.el-range-editor--medium .el-range-separator {
  line-height: 28px;
  font-size: 14px;
}
.el-range-editor--medium .el-range-input {
  font-size: 14px;
}
.el-range-editor--medium .el-range__icon, .el-range-editor--medium .el-range__close-icon {
  line-height: 28px;
}
.el-range-editor--small.el-input__inner {
  height: 32px;
}
.el-range-editor--small .el-range-separator {
  line-height: 24px;
  font-size: 13px;
}
.el-range-editor--small .el-range-input {
  font-size: 13px;
}
.el-range-editor--small .el-range__icon, .el-range-editor--small .el-range__close-icon {
  line-height: 24px;
}
.el-range-editor--mini.el-input__inner {
  height: 28px;
}
.el-range-editor--mini .el-range-separator {
  line-height: 20px;
  font-size: 12px;
}
.el-range-editor--mini .el-range-input {
  font-size: 12px;
}
.el-range-editor--mini .el-range__icon, .el-range-editor--mini .el-range__close-icon {
  line-height: 20px;
}
.el-range-editor.is-disabled {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
  color: #c0c4cc;
  cursor: not-allowed;
}
.el-range-editor.is-disabled:hover, .el-range-editor.is-disabled:focus {
    border-color: #e4e7ed;
}
.el-range-editor.is-disabled input {
    background-color: #f5f7fa;
    color: #c0c4cc;
    cursor: not-allowed;
}
.el-range-editor.is-disabled input::-webkit-input-placeholder {
      color: #c0c4cc;
}
.el-range-editor.is-disabled input:-ms-input-placeholder {
      color: #c0c4cc;
}
.el-range-editor.is-disabled input::placeholder {
      color: #c0c4cc;
}
.el-range-editor.is-disabled .el-range-separator {
    color: #c0c4cc;
}

