  z-index: 2000;
  font-size: 12px;
  line-height: 1.2;
}
.el-tooltip__popper .popper__arrow, .el-tooltip__popper .popper__arrow::after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
