}
.el-textarea.is-disabled .el-textarea__inner::-webkit-input-placeholder {
      color: #c0c4cc;
}
.el-textarea.is-disabled .el-textarea__inner:-ms-input-placeholder {
      color: #c0c4cc;
}
.el-textarea.is-disabled .el-textarea__inner::placeholder {
      color: #c0c4cc;
}
.el-input-number {
  position: relative;
  display: inline-block;
  width: 180px;
  line-height: 38px;
}
.el-input-number .el-input {
    display: block;
}
.el-input-number .el-input__inner {
      -webkit-appearance: none;
      padding-left: 50px;
      padding-right: 50px;
      text-align: center;
}
.el-input-number__increase, .el-input-number__decrease {
    position: absolute;
    z-index: 1;
    top: 1px;
    width: 40px;
    height: auto;
    text-align: center;
    background: #f5f7fa;
    color: #606266;
    cursor: pointer;
    font-size: 13px;
}
.el-input-number__increase:hover, .el-input-number__decrease:hover {
      color: #46a0f0;
}
.el-input-number__increase:hover:not(.is-disabled) ~ .el-input .el-input__inner:not(.is-disabled), .el-input-number__decrease:hover:not(.is-disabled) ~ .el-input .el-input__inner:not(.is-disabled) {
        border-color: #46a0f0;
}
